@import url(https://fonts.google.com/specimen/Open+Sans&display=swap);
/* background-color: rgb(218, 245, 240); */
body {
  background: #f3f3f3;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


/* $minWidth: 320px; */

.grid-container {
  max-width: 1440px;
  /* max-height: 600px; */
  display: grid;
  grid-template-areas: "header" "content";
  grid-template-columns: 1fr;
  margin: 50px auto;
  background: #fafaf9;
  font-family: "Open+Sans";
  }

.header {
  height: 80px;
  margin: auto 10px;
  grid-area: header;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  border-bottom: 0.1rem #e0e0e0 solid;
}

.checkList {
  display: -webkit-flex;
  display: flex;
  float: left;
  width: 270px;
}

.content{
  grid-area: content;
  display: -webkit-flex;
  display: flex;
  direction: ltr;
  direction: initial;
  min-height: 900px;
}

.main {
  display: -webkit-flex;
  display: flex;
  float: left;
}

.button { 
  border: none;
  border-radius: 5px;
  padding: 7px 12px;
  background: #62b4f8;
  cursor: pointer;
  text-transform: uppercase;
  font-weight: bold;
  color: white;
}
.button:hover { 
  background: #2196F3;
} 
.button:disabled,
.button[disabled]{
border: none;
background-color: #ccc;
color: #666666;
}


.stepContainer {
        -webkit-flex: 1 1;
                flex: 1 1;
}

.pointContainer {
    --primary-color: #2f80ed;
    --secondary-color: #e0e0e0;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.point {
        min-width: 40px;
        width: 40px;
        height: 40px;
        border-radius: 50%;
        font-size: 28px;
        color: #b3adad;
        text-align: center;
        background-color: var(--secondary-color);
        border: thick double #e0e0e0;
    }

.point i {
        vertical-align: middle;
    }  

.pre {
        height: 1px;
        width: 100%;
        margin-top: 18px;
        background-color: var(--secondary-color);
    }
.post {
        height: 1px;
        width: 100%;
        margin-top: 18px;
        background-color: var(--secondary-color);
    }
.done {
        background-color: var(--primary-color);
        color: #e0e0e0;        
}
.inactive {
        background-color: var(--secondary-color);
}
.current{
        border: thick double var(--primary-color);
        color: #2f80ed;
}
.hide {
        visibility: hidden;
   }
.stepLabel {
        height: 1rem;
        min-width: 260px;
        text-align: center;
    }

/* The switch - the box around the slider */
.switch {
    position: relative;
    display: inline-block;
    width: 38px;
    height: 23px;
  }
  
  /* Hide default HTML checkbox */
  .switch input {
    opacity: 0;
    width: 0;
    height: 0;
  }
  
  /* The slider */
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    transition: .4s;
  }
  
  .slider:before {
    position: absolute;
    content: "";
    height: 19px;
    width: 19px;
    left: 3px;
    bottom: 2px;
    background-color: white;
    /* background: #fafaf9; */
    transition: .4s;
  }
  
  input:checked + .slider {
    background-color: #2196F3;
  }
  
  input:focus + .slider {
    box-shadow: 0 0 1px #2196F3;
  }
  
  input:checked + .slider:before {
    -webkit-transform: translateX(13px);
    transform: translateX(13px);
  }
  
  /* Rounded sliders */
  .slider.round {
    border-radius: 16px;
  }
  
  .slider.round:before {
    border-radius: 50%;
  }

  
