
.stepContainer {
        flex: 1;
}

.pointContainer {
    --primary-color: #2f80ed;
    --secondary-color: #e0e0e0;
    display: flex;
    justify-content: space-between;
}

.point {
        min-width: 40px;
        width: 40px;
        height: 40px;
        border-radius: 50%;
        font-size: 28px;
        color: #b3adad;
        text-align: center;
        background-color: var(--secondary-color);
        border: thick double #e0e0e0;
    }

.point i {
        vertical-align: middle;
    }  

.pre {
        height: 1px;
        width: 100%;
        margin-top: 18px;
        background-color: var(--secondary-color);
    }
.post {
        height: 1px;
        width: 100%;
        margin-top: 18px;
        background-color: var(--secondary-color);
    }
.done {
        background-color: var(--primary-color);
        color: #e0e0e0;        
}
.inactive {
        background-color: var(--secondary-color);
}
.current{
        border: thick double var(--primary-color);
        color: #2f80ed;
}
.hide {
        visibility: hidden;
   }
.stepLabel {
        height: 1rem;
        min-width: 260px;
        text-align: center;
    }
